<template>
  <div>
  <el-alert title="你已取消删除操作" v-if="cancel" type="error" center show-icon>
  </el-alert>
  <el-alert title="请稍等文件下载中..." v-if="download" type="info" center show-icon>
  </el-alert>
  <el-alert title="删除成功" v-if="delSuccess" type="success" center show-icon>
  </el-alert>
    <el-table :data="registrationList" height="490" border stripe style="width: 100%">
      <el-table-column sortable prop="opDay" width="100px" label="日期"></el-table-column>
      <el-table-column prop="opStart" width="140px" label="开始时间"></el-table-column>
      <el-table-column prop="opEnd" width="140px" label="结束时间"></el-table-column>
      <el-table-column prop="opType" label="变更类型"></el-table-column>
      <el-table-column prop="changedSystem" label="变更系统"></el-table-column>
      <el-table-column prop="operateContent" width="190px" label="操作内容"></el-table-column>
      <el-table-column sortable prop="operator" width="100px" label="操作人"></el-table-column>
      <el-table-column sortable prop="demander" width="100px" label="需求人"></el-table-column>
      <el-table-column prop="note" label="备注" width="150px"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="medium" type="primary" plain @click="toUpdatePage(scope.row.id)">编辑</el-button>
          <br/>
          <el-button size="medium" type="danger" plain @click="deleteRegistration(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background :current-page="curP"	@current-change="getPageData" layout="prev, pager, next" :total="dataCount">
    </el-pagination>
    <el-button type="primary" @click="toAddPage">前往添加</el-button>
    <el-button type="warning" @click="exportFile">导出文件</el-button>
  </div>
</template>

<script>
import Vue from "vue";
import $ from "jquery";
export default {
  name: "Test",
  data() {
    return {
      cancel: false,
      download: false,
      delSuccess: false,
      curP: 1,
      dataCount: 0,
      registrationList: [],
      targetURL:'172.16.221.66:8089'
    };
  },
  mounted: function () {
    this.getSearch();
  },
  methods: {
    getSearch: function () {
      var that = this;
      $.ajax({
        type: "get",
        url: process.env.VUE_APP_API_URL+"/getAll",
        async: true,
        success: function (data) {
          // that.registrationList = data;
          that.registrationList=[];
          for(var i=0;i<Math.min(10,data.length);i++){
              that.registrationList.push(data[i]);
          }
          that.dataCount = data.length;

        },error:function(){
          that.$notify.error({
          title: '错误',
          message: '发生异常'
        });
        }
      });
    },
    deleteRegistration: function (targetId) {
      var that=this;
      if (confirm("确认删除吗？")) {
        $.ajax({
          type: "delete",
          dataType: "json",
          url: process.env.VUE_APP_API_URL+"/delById?id=" + targetId,
          success: function (data) {
            // window.location.reload();
            that.dataCount = that.dataCount-1;
            that.delSuccess = true;
            if(that.curP>(that.dataCount+9)/10){
              that.curP = that.curP-1;
            }
            that.getPageData(that.curP);
            setTimeout(function(){
            that.delSuccess=false;
          },2000);
          },error:function(){
          that.$notify.error({
          title: '错误',
          message: '发生异常'
        });
        }
        });
      } else {
          that.cancel=true;
          setTimeout(function(){
            that.cancel=false;
          },2000);
        }
    },
    getPageData: function(val){
      var that=this;
      that.curP=val;
      var dd=10*val-10;
      $.ajax({
        type:"get",
        url:process.env.VUE_APP_API_URL+"/list?pageNo="+dd,
        async:true,
        success: function(data){
          that.registrationList=[];
          console.log(data);
          for(var i=0;i<data.length;i++){
            that.registrationList.push(data[i]);
          }
        },error:function(){
          that.$notify.error({
          title: '错误',
          message: '发生异常'
        });
        }
      });
    },
    exportFile:function(){
      var that = this;
      that.download=true;
      window.location.href=process.env.VUE_APP_API_URL+"/export";
      setTimeout(function(){
            that.download = false;
          },2000);
    },
    toAddPage: function () {
      window.location.href = "/#/";
    },
    toUpdatePage: function (pid) {
      // var targetTr = document.getElementsByTagName('tr')[index+1];
      // var childTds = targetTr.children;
      // for(var i=0;i<targetTr.childElementCount-1;i++){
      //     var child = childTds[i];
      //     child.setAttribute("contenteditable", "true");
      // }
      // var pid = this.registrationList[index].id;
      window.location.href = "/#/update?id=" + pid;
    },
  },
};
</script>