<template>
  <div>
  <el-form ref="classObject" :model="classObject" label-width="100px">
      <el-form-item label="操作日期">
        <el-col :span="8">
          <el-input type="date" id="day" name="opDay"></el-input>
        </el-col>
      </el-form-item>

      <el-form-item align="left" label="变更时间段">
        <el-select id="sel" v-model="period" placeholder="请选择">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="开始时间">
        <el-col :span="8">
          <el-input type="datetime-local" id="start" name="opStart"/>
        </el-col>
      </el-form-item>

      <el-form-item label="结束时间">
        <el-col :span="8">
          <el-input type="datetime-local" id="end" name="opEnd"/>
        </el-col>
      </el-form-item> -->
      <el-form-item label="操作类型" align="left">
        <el-checkbox-group v-model="classObject.opType">
          <el-checkbox label="服务器变更"></el-checkbox>
          <el-checkbox label="域名变更"></el-checkbox>
          <el-checkbox label="数据库变更"></el-checkbox>
          <el-checkbox label="配置变更"></el-checkbox>
          <el-checkbox label="网络变更"></el-checkbox>
          <el-checkbox label="其他变更"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="变更系统">
        <el-col :span="8">
          <el-input v-model="classObject.changedSystem" id="changedSystem" clearable/>
        </el-col>
      </el-form-item>

      <el-form-item label="变更内容">
        <el-col :span="8">
          <el-input type="textarea" v-model="classObject.operateContent" id="operateContent"/>
        </el-col>
      </el-form-item>

      <el-form-item label="操作人">
        <el-col :span="8">
          <el-input type="text" v-model="classObject.operator" clearable id="operator"/>
        </el-col>
      </el-form-item>

      <el-form-item label="需求人">
        <el-col :span="8">
          <el-input type="text" v-model="classObject.demander" clearable id="demander"/>
        </el-col>
      </el-form-item>

      <el-form-item label="备注">
        <el-col :span="8">
          <el-input type="textarea" v-model="classObject.note" id="note"/>
        </el-col>
      </el-form-item>
    </el-form>

    <el-button type="success" @click="submitForAdd">提交</el-button>
    <el-button type="info" @click="toSearchPage">查询全部</el-button>
    <!-- <p>{{ classObject }}</p> -->
  </div>
</template>
<script>
import Vue from "vue";
import $ from "jquery";
export default {
  name: "Update",
  data() {
    return {
      classObject: {
        id: 0,
        opDay: null,
        opStart: null,
        opEnd: null,
        opType: [],
        changedSystem: null,
        operateContent: null,
        operator: null,
        demander: null,
        note: null,
      },
      options:[
        {value:1,label:"13:30-14:30"},
        {value:2,label:"14:30-15:30"},
        {value:3,label:"15:30-16:30"},
        {value:4,label:"16:30-17:00"},
        {value:5,label:"23:00-06:00"},
        {value:6,label:"已申请非发布窗口期变更"}
      ],
      pushContent:{
        msgtype:"markdown",
        markdown:{
          content:""
        }
      },
      period:null,
      targetURL:'172.16.221.66:8089'
    };
  },
  mounted: function () {
    var addrstr = location.href;
    var targetId = addrstr.substring(
      addrstr.lastIndexOf("?") + 4,
      addrstr.length
    );
    var that = this;
    $.ajax({
      type: "get",
      dataType: "json",
      url: process.env.VUE_APP_API_URL+"/getById?id=" + targetId,
      async: true,
      success: function (data) {
        that.classObject.id = targetId;
        that.classObject.operator = data.operator;
        that.classObject.demander = data.demander;
        that.classObject.note = data.note;
        $("#day").val(data.opDay);
        that.classObject.opDay=data.opDay;
        that.classObject.changedSystem = data.changedSystem;
        that.classObject.operateContent = data.operateContent;
        // $("#start").val(data.opStart.replace(" ", "T"));
        // $("#end").val(data.opEnd.replace(" ", "T"));
        if(data.opStart==null){
          $("#sel").val("已申请非发布窗口期变更");
        }
        else{
          switch(data.opStart.substr(11)){
          case "13:30":$("#sel").val("13:30-14:30");that.period=1;break;
          case "14:30":$("#sel").val("14:30-15:30");that.period=2;break;
          case "15:30":$("#sel").val("15:30-16:30");that.period=3;break;
          case "16:30":$("#sel").val("16:30-17:00");that.period=4;break;
          case "23:00":$("#sel").val("23:00-06:00");that.period=5;break;
        }
        }
        
        var types = data.opType.split(",");

        for (var i = 0; i < types.length; i++) {
          that.classObject.opType[i] = types[i];
        }
      },error:function(){
          that.$notify.error({
          title: '错误',
          message: '发生异常'
        });
        }
    });
  },
  methods: {
    check: function () {
      if ($("#day").val()!= null&&this.period!=null&&this.classObject.changedSystem!=null&&
          this.classObject.changedSystem.trim()!=''&&this.classObject.operateContent!=null&&
          this.classObject.operateContent.trim()!=''&&this.classObject.operator!=null&&
          this.classObject.operator.trim()!=''&&this.classObject.demander!=null&&
          this.classObject.demander.trim()!=''&&this.classObject.opType.length!=0) {
        this.classObject.opDay = $("#day").val() + " 00:00:00";
        switch(this.period){
          case 1:
            this.classObject.opStart=$("#day").val()+" 13:30:00";
            this.classObject.opEnd=$("#day").val()+" 14:30:00";
            break;
          case 2:
            this.classObject.opStart=$("#day").val()+" 14:30:00";
            this.classObject.opEnd=$("#day").val()+" 15:30:00";
            break;
          case 3:
            this.classObject.opStart=$("#day").val()+" 15:30:00";
            this.classObject.opEnd=$("#day").val()+" 16:30:00";
            break;
          case 4:
            this.classObject.opStart=$("#day").val()+" 16:30:00";
            this.classObject.opEnd=$("#day").val()+" 17:00:00";
            break;
          case 5:
            this.classObject.opStart=$("#day").val()+" 23:00:00";
            this.classObject.opEnd=$("#day").val()+" 06:00:00";
            break;
          default:
            this.classObject.opStart=null;
            this.classObject.opEnd=null;
            break;
        }
        // this.classObject.opStart = $("#start").val().replace("T", " ") + ":00";
        // this.classObject.opEnd = $("#end").val().replace("T", " ") + ":00";
        return true;
      } else return false;
    },
    testPush: function(){
      var that = this;
      that.pushContent.markdown.content="## 登记数据修改\n"
          +"变更时间段："+((that.classObject.opStart!=null&&that.classObject.opEnd!=null)?
          ("<font color=\"comment\">"+that.classObject.opStart+"-"+that.classObject.opEnd.substring(11))+"</font>":"<font color=\"comment\">"+that.classObject.opDay.substring(0,10)+" 已申请非发布窗口期变更</font>")
          +"\n >变更类型：<font color=\"warning\">"+that.classObject.opType+"</font>"
          +"\n >变更系统：<font color=\"info\">"+that.classObject.changedSystem+"</font>"
          +"\n >操作内容：<font color=\"comment\">"+that.classObject.operateContent+"</font>"
          +"\n >操作人：**"+that.classObject.operator+"**"
          +"\n >需求人：**"+that.classObject.demander+"**"
          +"\n >影响范围：<font color=\"comment\">"+that.classObject.note+"</font>";
      axios.post(`/api/cgi-bin/webhook/send?key=d3dfb6a6-e884-48e9-90f6-701bbb40352c`,that.pushContent)
           .then(res=>{
            console.log('res=>',res);            
    }).catch(error=>{
      console.log(error);
    })
      
    },
    submitSuccess:function(){
      this.$alert('你已成功修改', '通知', {
          confirmButtonText: '确定',
          callback: action => {
            this.$message({
              type: 'info',
              message: `操作成功`
            });
          }
        });
    },
    submitForAdd: function () {
      if(this.check()){
        var that = this;
      console.log(that.classObject.id);
      $.ajax({
        type: "POST",
        dataType: "text",
        url: process.env.VUE_APP_API_URL+"/updateChange",
        data: that.classObject,
        success: function (result) {
          // that.testPush();   
          console.log("success");
          that.submitSuccess();
        },error:function(){
          that.$notify.error({
          title: '错误',
          message: '发生异常'
        });
        }
      });
      }
      else{
        alert("请完整填写（可不填写备注）");
      }
      
    },

    toSearchPage: function() {
        window.location.href = '/#/search';
    }
  },
};
</script>