import Vue from 'vue'
import Router from 'vue-router'
import Add from '@/components/Add'
import Search from '@/components/Search'
import Update from '@/components/Update'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Add',
      component: Add
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/update',
      name: 'Update',
      component: Update
    }
  ]
})
